// 二维码相关 Api

/**
 * 二维码下载
 * @param {String} id 元素 id 
 * @param {String} name 下载二维码的文件名
 */
export const qrcodeDownload = (id, name) => {
  // 构造标签模拟点击
  const link = document.createElement('a');
  link.download = name;
  link.href = document.getElementById(id).toDataURL('image/png');
  link.click()
  link.remove();
}